* {
  margin: 0;
  font-family: "Roboto", "Manjari", Helvetica, sans-serif;
}

html {
  scroll-behavior: smooth;
}

.section-content {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  max-width: 1000px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.wave svg {
  position: relative;
  display: block;
  width: 100%;
  height: 100px;
}
